<template>
  <div class="register-view background-container">
    <div class="container p-5 flex-center">
      <div class="card p-5 card-glassmo my-5 w-75">
        <form class="form">
          <div class="row flex-center">
            <div class="col-12 form-text card-title">
              <h3>register as doctor</h3>
            </div>
            <div class="col-12 my-3 card-text">
              <input
                class="form-control"
                type="text"
                placeholder="First name"
                v-model="fname"
              />
            </div>
            <div class="col-12 card-text">
              <input
                class="form-control"
                type="text"
                placeholder="Last name"
                v-model="lname"
              />
            </div>
            <div class="col-12 card-text">
              <div class="card-text text-start">Birthday</div>
              <input class="form-control" type="date" v-model="birthday" />
            </div>
            <div class="col-12 my-3 card-text">
              <input
                class="form-control"
                type="text"
                placeholder="email"
                v-model="email"
              />
            </div>
            <div class="col-12 my-3 card-text">
              <input
                class="form-control"
                type="password"
                placeholder="password"
                v-model="password"
              />
            </div>
            <div class="col-12 card-text">
              <input
                class="form-control"
                type="password"
                placeholder="check password"
                v-model="checkpassword"
              />
            </div>
            <div class="col-12 my-3 card-text">
              <input
                class="form-control"
                type="text"
                placeholder="address"
                v-model="address"
              />
            </div>
            <div class="col-12 card-text">
              <input
                class="form-control"
                type="text"
                placeholder="phone number"
                v-model="phone"
              />
            </div>
            <div class="col-12 my-3 card-text">
              <div class="d-block">
                <div class="radio-input">
                  <input
                    type="radio"
                    name="gender"
                    value="Male"
                    @click="gender = 'Male'"
                  />
                  <div class="circle"></div>
                  <span class="me-4">Male</span>
                  <input
                    type="radio"
                    name="gender"
                    value="Female"
                    @click="gender = 'Female'"
                  />
                  <div class="circle"></div>
                  <span>Female</span>
                </div>
              </div>
            </div>
            <div class="col-12 card-text">
              <div class="dropdown">
                <button
                  class="btn btn-outline-dark dropdown-toggle w-100"
                  type="button"
                  id="triggerId"
                  data-bs-toggle="dropdown"
                  aria-haspopup="true"
                  aria-expanded="false"
                >
                  {{ specialization }}
                </button>
                <div
                  class="dropdown-menu w-100 drop-scroller"
                  aria-labelledby="triggerId"
                >
                  <button
                    class="dropdown-item w-100 text-center"
                    type="button"
                    v-for="element in specializations"
                    :key="element.id"
                    @click="
                      specialization = element.name;
                      specialization_id = element.id;
                    "
                    :style="{
                      color:
                        specialization_id == element.id
                          ? 'rgb(74, 226, 146)'
                          : 'rgb(0,0,0)',
                    }"
                  >
                    {{ element.name }}
                  </button>
                </div>
              </div>
            </div>
            <div class="col-12 my-3 card-text">
              <div class="card-text text-start">certificate</div>
              <input
                class="form-control"
                type="file"
                accept="image/*"
                id="certificate"
                @change="addcertificate()"
              />
            </div>
            <div class="col-12 card-text">
              <div class="card-text text-start">certificate for work</div>
              <input
                class="form-control"
                type="file"
                accept="image/*"
                id="certificate2"
                @change="addcertificate2()"
              />
            </div>
            <div class="col-12 my-3 card-text">
              <div class="card-text text-start">signature</div>
              <input
                class="form-control"
                type="file"
                accept="image/*"
                id="signature"
                @change="addsignature()"
              />
            </div>
            <div class="col-4 card-text">
              <button
                type="button"
                class="btn btn-outline-dark"
                style="transition: 0.5s ease-in-out"
                @click="register()"
              >
                register
              </button>
            </div>
            <div class="col-12 text-center card-text my-3">
              <router-link to="/" class="register">Login</router-link>
            </div>
          </div>
        </form>
      </div>
    </div>
  </div>
  <div class="overlay" id="popup1" style="visibility: hidden; opacity: 0">
    <div class="popup">
      <div class="content desc p-5 mt-2">
        <h4>Your request is pending</h4>
      </div>
      <div class="content desc w-100 flex-center">
        <loading-page></loading-page>
      </div>
      <div class="content desc p-5">
        <h5>{{ timer }}</h5>
      </div>
    </div>
  </div>
</template>

<script>
// @ is an alias to /src
import LoadingPage from "@/components/LoadingPage.vue";
import router from "@/router";
import axios from "axios";
export default {
  name: "register-view",
  data() {
    return {
      lname: "",
      password: "",
      checkpassword: "",
      email: "",
      fname: "",
      address: "",
      gender: "",
      phone: "",
      birthday: "",
      specialization: "specialization",
      specialization_id: 0,
      certificate: "",
      certificate2: "",
      signature: "",
      specializations: {
        0: {
          id: 1,
          name: "eye",
        },
        1: {
          id: 2,
          name: "teeth",
        },
      },
      timer: 10,
    };
  },
  components: {
    LoadingPage,
  },
  methods: {
    addcertificate() {
      var input = document.querySelector("#certificate");
      this.certificate = input.files[0];
    },
    addcertificate2() {
      var input = document.querySelector("#certificate2");
      this.certificate2 = input.files[0];
    },
    addsignature() {
      var input = document.querySelector("#signature");
      this.signature = input.files[0];
    },
    calchour() {
      if (this.timer == 0) {
        router.push("/");
        return;
      } else {
        setTimeout(() => {
          this.timer--;
          this.calchour();
        }, 1000);
      }
    },
    show_hide() {
      const element2 = document.getElementById("popup1");
      element2.style.visibility = "visible";
      element2.style.opacity = 1;
      this.calchour();
    },
    register() {
      if (this.fname == "") {
        window.alert("First name is empty");
        return;
      } else if (this.lname == "") {
        window.alert("Last name is empty");
        return;
      } else if (this.email == "") {
        window.alert("Email is empty");
        return;
      } else if (this.address == "") {
        window.alert("address is empty");
        return;
      } else if (this.birthday == "") {
        window.alert("birthday is empty");
        return;
      } else if (this.password == "") {
        window.alert("password is empty");
        return;
      } else if (this.checkpassword == "") {
        window.alert("confirm passwod is empty");
        return;
      } else if (this.password != this.checkpassword) {
        window.alert("password don't match");
        return;
      } else if (this.gender == "") {
        window.alert("You should choose a gender");
        return;
      } else if (this.phone == "") {
        window.alert("Phone is empty");
        return;
      } else if (this.specialization_id == "") {
        window.alert("Choose a specialization");
        return;
      } else if (this.certificate == "") {
        window.alert("Upload a certificate");
        return;
      } else if (this.certificate2 == "") {
        window.alert("Upload a certificate for work");
        return;
      } else if (this.signature == "") {
        window.alert("Upload a signature");
        return;
      }

      const formData = new FormData();
      const imgFile = document.querySelector("#certificate");
      const imgFile2 = document.querySelector("#certificate2");
      const imgFile3 = document.querySelector("#signature");
      formData.append("Certificate", imgFile.files[0]);
      formData.append("Certificate_For_Work", imgFile2.files[0]);
      formData.append("Signature", imgFile3.files[0]);
      formData.append("fname", this.fname);
      formData.append("lname", this.lname);
      formData.append("email", this.email);
      formData.append("address", this.address);
      formData.append("birthday", this.birthday);
      formData.append("password", this.password);
      formData.append("gender", this.gender);
      formData.append("phone", this.phone);
      formData.append("specialization_id", this.specialization_id);
      axios
        .post("http://127.0.0.1:8000/api/Register-Doctor", formData, {
          headers: {
            "Content-Type": "multipart/form-data",
          },
        })
        .then((response) => {
          response;
          this.show_hide();
        })
        .catch((error) => {
          console.log(error);
        });

      // router.push("/register_waiting");
    },
  },
};
</script>
