<template>
  <div class="register-waiting background-container">
    <div class="container vh-100 w-100 flex-center">
      <div class="row p-5 card-glassmo">
        <div class="col-12 my-1 flex-center">
          <h3>Waiting for your request</h3>
        </div>
        <div class="col-12 flex-center"><LoadingPage></LoadingPage></div>
        <div class="col-12 my-1 flex-center">
          <h5>{{ timer }} hour</h5>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import LoadingPage from "@/components/LoadingPage.vue";
export default {
  name: "register-waiting",
  data() {
    return {
      timer: 1,
    };
  },
  components: {
    LoadingPage,
  },
  methods: {
    calchour() {
      if (this.timer == 0) {
        return;
      } else {
        setTimeout(() => {
          this.timer--;
          this.calchour();
        }, 360000);
      }
    },
  },
  mounted() {
    this.calchour();
  },
};
</script>
